@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
body {
  font-family: "Open Sans", serif;
}
.wrapper {
  background: -webkit-gradient(linear, left top, left bottom, from(#2980b9), color-stop(#6dd5fa), to(#fff));
  background: -webkit-linear-gradient(top, #2980b9, #6dd5fa, #fff);
  background: linear-gradient(to bottom, #2980b9, #6dd5fa, #fff);
  padding: 5vh 5vw;
  display: flex;
  height: 100vh;
  overflow: hidden;
}
.main {
  height: 100%;
  width: 100vw;
  background: rgba(255,255,255,0.3);
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-radius: 3vw;
  flex-direction: column;
  position: relative;
  text-align: center;
}
.title {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 20vh;
  top: 0;
  font-size: 2.7vmin;
  padding-bottom: 4vh;
}
.content {
  display: flex;
  width: 100%;
  height: 60vh;
  justify-content: flex-start;
  align-items: flex-start;
}
.content .left {
  width: 50%;
  height: 30vh;
}
.content .left form {
  width: 100%;
  height: 30vh;
  display: flex;
  justify-content: space-around;
  margin-top: 3vh;
  align-items: center;
  flex-direction: column;
  padding-top: 0;
  margin-top: 0;
}
.content .left form input {
  font-size: 2.5vmin;
  margin: 1%;
  padding: 2%;
  width: 35vw;
  height: 7vh;
  border-radius: 2vw;
  border: 0;
}
.content .left form button {
  width: 20vw;
  height: 5vh;
  font-size: 2.5vmin;
  border: 2px solid #000;
  border-radius: 2vw;
  background: transparent;
  margin: 1% 0;
}
.content .left form button:active {
  background: #000;
  color: White;
}
.content .weather {
  margin: 1% 0;
  height: 30vh;
  background: #6dd5fa;
  text-align: left;
  display: flex;
  justify-content: center;
  align-items: center;
}
.content .right {
  background: #fff;
  width: 50%;
  padding: 0 1%;
  height: 60vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.content .right .joke {
  width: 100%;
  height: 100%;
  padding: 12% 5%;
}
.content .right .joke display flex·,
.content .right .joke justify-content space-around,
.content .right .joke align-items center,
.content .right .joke flex-direction column,
.content .right .joke h5 {
  font-size: 2.8vmin;
  font-weight: 900;
  letter-spacing: 0.15em;
}
.content .right .joke h3 {
  padding: 5%;
  font-size: 2.3vmin;
}
.content .right .joke .jokeTitle {
  font-size: 2.5vmin;
}
.content .right .joke .jokeAns {
  color: #f00;
  font-weight: 900;
}
@media screen and (max-width: 768px) {
  .content {
    width: 100%;
    flex-wrap: wrap;
  }
  .content .left,
  .content .right {
    width: 100%;
  }
}

